<template>

  <div class="accordion">
    <div class="accordion__container-btn">
      <button 
        v-for="(el, index) in header" :key="index"
        @click="curSlot = index + 1"
        class="accordion__btn" 
      > 
        {{ el }}
      </button>
    </div>

    <div class="accordion__container-info">
      <slot :name="curSlot"></slot>
    </div>
  </div>

</template>

<script>
export default {
  name: 'vAccordion',

  props: {
    header: {
      required: true,
      type: Array
    }
  },

  data: () => ({
    curSlot: 1
  }),

  watch: {

  },

  methods: {
    
  },

  computed: {

  },

  created() {
    
  },
}
</script>

