<template>
  <section class="page admin" v-cloak>

    <v-accordion
      :header="[
        'Товары', 'Категории', 'Возраст. ограничения',
      ]"
    >
      <template v-slot:1>
        <v-table
          :header="[
            { 
              width: '250px', type: 'text', edit: true, name: 'Название', path: 'title', subpath: null, prefix: null
            },

            { 
              width: '450px', type: 'text', edit: true, name: 'Описание', path: 'description', subpath: null, prefix: null
            },

            { 
              type: 'img', edit: true, name: 'Картинка', path: 'img', subpath: null, prefix: null
            },

            { 
              type: 'list', edit: false, name: 'Возраст', path: 'ageLimit', subpath: 'name', prefix: '+',
              list: getAllAgeLimit,
              head: [
                { name: 'Номер', path: 'id', subpath: null, prefix: null },
                { name: 'Название', path: 'name', subpath: null, prefix: null },
                { name: 'Цвет', path: 'color', subpath: null, prefix: null },
              ], 
            },

            { 
              type: 'list', edit: false, name: 'Кратегория', path: 'category', subpath: 'name', prefix: null,
              list: getAllCategory,
              head: [
                { name: 'Номер', path: 'id', subpath: null, prefix: null },
                { name: 'Название', path: 'name', subpath: null, prefix: null },
                { name: 'Цвет', path: 'color', subpath: null, prefix: null },
              ],
            },

            { 
              type: 'number', edit: true, name: 'Количество', path: 'quantityStore', subpath: null, prefix: null
            },

            { 
              type: 'number', edit: true, name: 'Цена за шт', path: 'price', subpath: null, prefix: '$'
            },
          ]"

          :body="getAllProduct"
          ref="tableProduct"
        ></v-table>

        <div class="admin__container-btn">
          <button class="admin__btn" @click="deleteProduct">УДАЛИТЬ</button>
          <button class="admin__btn" @click="$refs.tableProduct.addNewRow()">ДОБАВИТЬ</button>
          <button class="admin__btn" @click="saveProduct">СОХРАНИТЬ</button>
        </div>
      </template>

      <template v-slot:2>
        <v-table
          :header="[
            { name: 'Номер', path: 'id', subpath: null, prefix: null },
            { type: 'text', edit: true, name: 'Название', path: 'name', subpath: null, prefix: null },
            { type: 'text', edit: true, name: 'Цвет', path: 'color', subpath: null, prefix: null },
          ]"

          :body="getAllCategory"
          ref="tableCategory"
        ></v-table>

        <div class="admin__container-btn">
          <button class="admin__btn" @click="deleteCategory">УДАЛИТЬ</button>
          <button class="admin__btn" @click="$refs.tableCategory.addNewRow()">ДОБАВИТЬ</button>
          <button class="admin__btn" @click="saveCategory">СОХРАНИТЬ</button>
        </div>
      </template>

      <template v-slot:3>
        <v-table
          :header="[
            { name: 'Номер', path: 'id', subpath: null, prefix: null },
            { type: 'number', edit: true, name: 'Название', path: 'name', subpath: null, prefix: null },
            { type: 'text', edit: true, name: 'Цвет', path: 'color', subpath: null, prefix: null },
          ]"

          :body="getAllAgeLimit"
          ref="tableAgeLimit"
        ></v-table>

        <div class="admin__container-btn">
          <button class="admin__btn" @click="deleteAgeLimit">УДАЛИТЬ</button>
          <button class="admin__btn" @click="$refs.tableAgeLimit.addNewRow()">ДОБАВИТЬ</button>
          <button class="admin__btn" @click="saveAgeLimit">СОХРАНИТЬ</button>
        </div>
      </template>
    </v-accordion>

  </section>
</template>

<script>
import vAccordion from '@/scripts/components/vAccordion.vue'
import vTable from '@/scripts/components/vTable.vue'
import { mapActions, mapGetters, mapMutations } from 'vuex'

export default {
  name: 'AdminView',

  data: () => ({
    
  }),

  watch: {

  },
  
  methods: {
    ...mapActions([
      'deleteCategoryInDb',
      'deleteAgeLimitInDb',
      'deleteProductInDb',
      'updateDbCategory',
      'updateDbAgeLimit',
      'updateDbProduct',
    ]),

    ...mapMutations([
      'setError'
    ]),

    saveProduct() {
      const data = this.$refs.tableProduct.renderData()

      if( data?.message ) {
        this.setError( data )
        return
      }

      this.updateDbProduct( data )
    },
    
    saveCategory() {
      const data = this.$refs.tableCategory.renderData()

      if( data?.message ) {
        this.setError( data )
        return
      }

      this.updateDbCategory( data )
    },

    saveAgeLimit() {
      const data = this.$refs.tableAgeLimit.renderData()

      if( data?.message ) {
        this.setError( data )
        return
      }
      
      this.updateDbAgeLimit( data )
    },

    deleteProduct() {
      const data = this.$refs.tableProduct.deleteRow()

      if( data?.message ) {
        this.setError( data )
        return
      }

      this.deleteProductInDb( data )
    },

    deleteCategory() {
      const data = this.$refs.tableCategory.deleteRow()

      if( data?.message ) {
        this.setError( data )
        return
      }

      this.deleteCategoryInDb( data )
    },

    deleteAgeLimit() {
      const data = this.$refs.tableAgeLimit.deleteRow()

      if( data?.message ) {
        this.setError( data )
        return
      }

      this.deleteAgeLimitInDb( data )
    },
  },

  computed: {
    ...mapGetters([
      'getAllProduct',
      'getAllCategory',
      'getAllAgeLimit',
    ]),
  },

  components: {
    'v-accordion': vAccordion,
    'v-table': vTable
  },

  created() {
  
  }
}
</script>