<template>
  <div>
    <table id="my-table">
      
      <thead>
        <tr>
          <th v-for="el in head" :key="el">{{ el.name }}</th>
        </tr>
      </thead>
      
      <tbody>
        <tr 
          v-for="(row, index) in rows" :key="index"
          :class="{ 'active': dropIndex === index, 'edit': row.editable }"
          @dragstart="!row.editable && dragStart(index, $event)" 
          @dragenter.prevent="dragEnter(index)" 
          @dragleave="dragLeave(index)" 
          @drop.prevent="drop(index)"
          @dragend="dragEnd($event)" 
          @dragover.prevent
          :draggable="true" 
        >
          <td 
            @dblclick.prevent="el.edit === true && startEdit( row, el.path, index )"
            v-for="(el, indexH) in head" :key="indexH" 
          >
            <p class="text" v-show="!row.editable" :style="{ width: el?.width }">
              {{ el.subpath ? row[ el.path ]?.[ el.subpath ] : row[ el.path ]  }}{{ el.prefix }}
            </p>

            <input
              :value="el.subpath ? row[ el.path ]?.[ el.subpath ] : row[ el.path ]"
              @keypress.enter.prevent="stopEdit( row, index )"
              @keypress.esc.prevent="stopResetEdit( row )"
              v-show="row.editable && el.edit === true"
              autofocuss="true"
              :type="el.type"
              :ref="el.path"
            >

            <v-list 
              v-if="row.editable && el.type === 'list'"
              :ref="`list-${ indexH }`"
              @selectRow="selectRow"
              :cIndex="indexH"
              :head="el.head"
              :rows="el.list"
              :path="el.path"
              :rIndex="index"
              :row="row"
            >
            </v-list>
          </td>
        </tr>
      </tbody>

    </table>
  </div>
</template>

<script>
import { mapMutations } from 'vuex';
import vList from './vList.vue';

export default {
  name: 'vTable',

  props: {
    header: {
      required: true,
      type: Array
    },

    body: {
      required: true,
      type: Array
    },
  },

  data: () => ({
    dragIndex: null,
    dropIndex: null,
    head: [],
    rows: [],
  }),

  methods: {
    ...mapMutations([
      'setError'
    ]),

    dragStart( index, event ) {
      this.dragIndex = index;
      event.dataTransfer.setData( "text/plain", index );
    },
    
    dragEnd() {
      this.dragIndex = null;
      this.dropIndex = null;
    },

    dragEnter( index ) {
      if (this.dragIndex !== null && index !== this.dragIndex) {
        this.dropIndex = index;
      }
    },

    dragLeave( index ) {
      if ( this.dropIndex === index ) {
        this.dropIndex = null;
      }
    },

    drop() {
      if ( this.dropIndex !== null ) {
        const draggedRow = this.rows[ this.dragIndex ];
        this.rows.splice( this.dragIndex, 1 );
        this.rows.splice( this.dropIndex, 0, draggedRow );
        this.dragIndex = null;
        this.dropIndex = null;
      }
    },

    startEdit( el, elRef, elIndex ) {
      this.rows.map( ( el, index ) => {
        el.editable === true && this.stopEdit( el, index )
      })

      el.editable = true

      this.$nextTick(() => {
        this.$refs[ elRef ][ elIndex ].focus()
      })
    },

    stopEdit( el, elIndex ) {
      this.head.forEach( ({ path, subpath, edit }) => {
        if( !edit ) return
        
        subpath
          ? this.rows[ elIndex ][ path ][ subpath ] = this.$refs[ path ][ elIndex ].value
          : this.rows[ elIndex ][ path ] = this.$refs[ path ][ elIndex ].value
      });

      el.editable = false
    },

    stopResetEdit( el ) {
      el.editable = false
    },

    selectRow( row, rIndex, cIndex, path, el ) {
      this.$refs[`list-${ cIndex }`][0].close()
      row[ path ] = el

      this.head.forEach( ({ path, subpath, edit }) => {
        if( !edit ) return
        
        subpath
          ? this.rows[ rIndex ][ path ][ subpath ] = this.$refs[ path ][ rIndex ].value
          : this.rows[ rIndex ][ path ] = this.$refs[ path ][ rIndex ].value
      });
    },

    addNewRow() {
      const findNewObj = this.rows.find( el => el.new )
      
      if( findNewObj ){
        this.setError({ 
          message: {
            success: [ 'У вас уже есть один добавленный но не сохраненный элемент. Сохраните и добавляйте новые!' ],
          },

          code: 401
        })

        return
      }

      const obj = { index: null, ...this.rows[0], new: true }

      Object
        .keys( obj )
        .forEach( key => {
          if ( key === 'editable' ) obj[ key ] = false
          else if ( key === 'new' ) obj[ key ] = true
          else obj[ key ] = null
        })
      
      console.log( obj );

      this.rows.unshift( obj )

      this.setError({ 
        message: {
          success: [ 'Новая строка добавлена в таблицу (^-^)' ],
        },

        code: 200
      })
    },

    renderData() {
      if( this.rows.some( el => el.editable ) ) return { 
        message: {
          success: [ 'Нельзя сохранить данные в режиме редоктирования' ],
        },

        code: 401
      }
      
      setTimeout(() => {
        this.tableMounted()
      }, 1500)

      return this.rows
    },

    deleteRow() {
      const data = this.rows.find( ( el ) => el.editable)

      if( !data ) return { 
        message: {
          success: [ 'Невозможно удалить не выбранную строку' ],
        },

        code: 401
      }

      setTimeout(() => {
        this.tableMounted()
      }, 1500)

      return data
    },

    tableMounted() {
      this.rows = this.body.map((row) => ({ ...row, editable: false, editField: null }));
    }
  },

  components: {
    'v-list': vList
  },

  created() {
    this.head = this.header
    this.rows = this.body
  },

  mounted() {
    this.tableMounted()
  }
};
</script>