<template>
  <div class="list">
    <p class="list__title" @click="show = true">показать список</p>
    
    <div class="list__body" v-if="show">
      <table class="list__table">
        <thead>
          <tr>
            <th v-for="el in head" :key="el">{{ el.name }}</th>
          </tr>
        </thead>
        
        <tbody>
          <tr v-for="(row, index) in rows" :key="index">
            <td 
              @click="$emit( 'selectRow', this.row, this.rIndex, this.cIndex, this.path, row )"
              v-for="(el, indexH) in head" :key="indexH" 
            >
              {{ el.subpath ? row[ el.path ]?.[ el.subpath ] : row[ el.path ]  }}{{ el.prefix }}
            </td>
          </tr>
        </tbody>
      </table>

      <button class="list__btn" @click="show = false">Выйти</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'vList',

  props: {
    head: {
      required: true,
      type: Array
    },

    rows: {
      required: true,
      type: Array
    },

    path: {
      required: true,
      type: String
    },

    cIndex: {
      required: true,
      type: Number
    },

    rIndex: {
      required: true,
      type: Number
    },

    row: {
      required: true,
      type: Object
    },
  },

  data: () => ({
    show: false
  }),

  watch: {

  },
  
  methods: {
    close() {
      this.show = false
    }
  },

  computed: {
    
  },

  components: {
    
  },

  created() {
  
  }
}
</script>